import React, { PropTypes } from 'react';
import Preloader from '../../common/Preloader';
import ClientsListTable from './ClientsListTable';

const ClientsListPage = ({ clients, permissions }) => {
  const preloader = (
    <div className="hic-content-preloader-wrapper">
      <Preloader />
    </div>
  );
  const clientsTable = (
    <ClientsListTable
      clients={clients}
      permissions={permissions}
    />
  );
  const nothingHere = <p>  </p>;

  let content;

  if (clients.loading) {
    content = preloader;
  } else if (clients.length) {
    content = clientsTable;
  } else {
    content = nothingHere;
  }

  return (
    <div>
      <h3></h3>
      {content}
    </div>
  );
};

ClientsListPage.propTypes = {
  clients: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number.isRequired,
      username: PropTypes.string.isRequired,
      realname: PropTypes.string,
      email: PropTypes.string,
      phone: PropTypes.string,
      balance: PropTypes.shape({
        RUB: PropTypes.number,
        USD: PropTypes.number,
        EUR: PropTypes.number,
      }),
      manager: PropTypes.shape({
        id: PropTypes.number.isRequired,
        realname: PropTypes.string.isRequired,
      }),
    }),
  ),
  permissions: PropTypes.shape({}),
};

export default ClientsListPage;



