/* eslint import/namespace: ['error', { allowComputed: true }]*/
import rulesManifest from './rulesManifest';

function ruleIsPassing(value, ruleName) {
  return !!rulesManifest[ruleName].rule(value);
}

function generateFieldError(value, ruleName) {
  const error = {};
  error.type = rulesManifest[ruleName].type;
  error.hint = rulesManifest[ruleName].hint(value);
  return error;
}


function hasErrors(valuesObject, schema) {
  let dataHasErrors = false;

  Object.keys(schema).forEach((field) => {
    schema[field].find((ruleName) => {
      if (!ruleIsPassing(valuesObject[field], ruleName)) {
        dataHasErrors = true;
        return true;
      }
      return false;
    });
  });

  return dataHasErrors;
}

function generateErrors(valuesObject, schema) {
  const errors = {};


  Object.keys(schema).forEach((field) => {
    schema[field].find((ruleName) => {
      if (!ruleIsPassing(valuesObject[field], ruleName)) {
        errors[field] = generateFieldError(valuesObject[field], ruleName);
        return true;
      }
      return false;
    });
  });

  return errors;
}

function generateError(field, value, schema) {
  const error = {};

  if (schema[field]) {
    schema[field].find((ruleName) => {
      if (!ruleIsPassing(value, ruleName)) {
        error[field] = generateFieldError(value, ruleName);
        return true;
      }

      error[field] = {};
      return false;
    });
  }

  return error;
}

export default {
  hasErrors,
  generateErrors,
  generateError,
};



