import React, { PropTypes } from 'react';
import { Grid, Col } from 'react-bootstrap';
import Header from './Header';
import Sidebar from './Sidebar';
import Preloader from '../Preloader';

const Layout = (props) => {
  const fullLayout = (
    <div className="hicLayout">
      <Header user={props.user} />
      <Grid fluid>
        <Col lg={3} md={3} sm={3} xs={12}>
          <Sidebar permissions={props.user.permissions} />
        </Col>
        <Col lg={9} md={9} sm={9} xs={12}>
          <div className="hicContent">
            {props.children}
          </div>
        </Col>
      </Grid>
    </div>
  );

  const layoutWithoutUser = (
    <div className="hicLayout">
      <Header />
      <Grid fluid>
        <div className="hicContent">
          {props.children}
        </div>
      </Grid>
    </div>
  );

  const preloader = (
    <div className="hicLayout">
      <Header user={props.user} />
      <div className="hicContent">
        <Preloader
          width={4}
          radius={50}
          lines={17}
        />
      </div>
    </div>
  );

  if (props.user.loading) {
    return preloader;
  } else if (props.user.authorized) {
    return fullLayout;
  }
  return layoutWithoutUser;
};

Layout.propTypes = {
  children: PropTypes.element,
  user: PropTypes.shape({
    loading: PropTypes.bool,
    id: PropTypes.number,
    authorized: PropTypes.bool,
    nickname: PropTypes.string,
    email: PropTypes.string,
    gender: PropTypes.string,
    super: PropTypes.bool,
    permissions: PropTypes.arrayOf(React.PropTypes.oneOfType([
      PropTypes.string,
      // eslint-disable-next-line
      PropTypes.object,
    ])),
    activity: PropTypes.arrayOf(React.PropTypes.shape({
      type: PropTypes.string,
      // eslint-disable-next-line
      object: PropTypes.object
    })),
  }),
};

export default Layout;



