import fetchival from 'fetchival';
import apiRoot from './apiRoot';

const user = fetchival(`${apiRoot}/user`);

class UserApiRemote {
  static getCurrentUser() {
    return new Promise((resolve, reject) => {
      user('current:unauthorized').get()
        .then((resp) => {
          resolve(resp);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }

  static authorizeUser(userData) {
    return new Promise((resolve, reject) => {
      user('auth').post(userData)
        .then((resp) => {
          resolve(resp);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }
}

export default UserApiRemote;



