import React, { PropTypes } from 'react';
import { ListGroup, Panel } from 'react-bootstrap';
import { Link } from 'react-router';
import values from 'lodash/values';
import bsStyles from '../../../utils/bsStyles';

const registeredSidebarLinks = {
  clients: {
    href: 'clients',
    title: '',
  },
  tokens: {
    href: 'tokens',
    title: '',
  },
  click: {
    href: 'click',
    title: '',
  },
  transactions: {
    href: 'transactions',
    title: '',
  },
  payment_history: {
    href: 'payment_history',
    title: ' ',
  },
  services: {
    href: 'services',
    title: '',
  },
  balance: {
    href: 'balance',
    title: '',
  },
  custom_packages: {
    href: 'custom_packages',
    title: ' ',
  },
  status: {
    href: 'status',
    title: '/',
  },
};
const registeredSidebarSectionTitles = {
  billing: {
    title: '',
    style: bsStyles.danger,
  },
  packages: {
    title: '',
    style: bsStyles.info,
  },
};

const Sidebar = ({ permissions }) => {
  const sections = {};
  const unsectioned = [];

  function translateSectionTitle(sectionCode) {
    return registeredSidebarSectionTitles[sectionCode] &&
      registeredSidebarSectionTitles[sectionCode].title;
  }

  function getSectionTitleStyle(sectionCode) {
    return registeredSidebarSectionTitles[sectionCode] &&
      registeredSidebarSectionTitles[sectionCode].style;
  }

  function createListGroupItem(permission) {
    if (registeredSidebarLinks[permission]) {
      const listGroupItemClass = 'list-group-item';
      return (
        <Link
          to={registeredSidebarLinks[permission].href}
          className={listGroupItemClass}
          activeClassName="active"
          key={permission}
        >
          {registeredSidebarLinks[permission].title}
        </Link>
      );
    }
    return false;
  }

  function createSidebarSection(permission) {
    const section = [];
    values(permission)[0].forEach((sectionPermission) => {
      const listGroupItem = createListGroupItem(sectionPermission);
      if (listGroupItem) {
        section.push(listGroupItem);
      }
    });
    sections[Object.keys(permission)[0]] = section;
  }

  function createReactListElements() {
    permissions.forEach((permission) => {
      if (typeof permission === 'string') {
        unsectioned.push(createListGroupItem(permission));
      } else if (typeof permission === 'object') {
        createSidebarSection(permission);
      }
    });
  }
  createReactListElements();

  return (
    <aside className="hicSidebar">
      {unsectioned.length &&
        <Panel>
          <ListGroup fill>
            {unsectioned.map(listGroupItem => listGroupItem)}
          </ListGroup>
        </Panel>
      }

      {Object.keys(sections).length && Object.keys(sections).map(sectionCode => (
        translateSectionTitle(sectionCode) &&
        <Panel
          key={sectionCode}
          header={translateSectionTitle(sectionCode)}
          bsStyle={getSectionTitleStyle(sectionCode)}
        >
          <ListGroup fill>
            {sections[sectionCode].map(listGroupItem => listGroupItem)}
          </ListGroup>
        </Panel>
      ))}
    </aside>
  );
};

Sidebar.contextTypes = {
  router: PropTypes.object,
};

Sidebar.propTypes = {
  permissions: PropTypes.arrayOf(React.PropTypes.oneOfType([
    PropTypes.string,
    // eslint-disable-next-line
    PropTypes.object,
  ])),
};

export default Sidebar;



