import React, { PropTypes } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Validation from '../../utils/forms/validation';
import * as paths from '../../routing/paths';
import * as userActions from '../../actions/userActions';
import LoginPage from './LoginPage';


// Export class unwrapped by connect for tests
export class ManageLoginPage extends React.Component {

  constructor(props, context) {
    super(props, context);
    this.state = {
      formDataUser: {},
      blurredFields: {},
      triedToSubmitForm: false,
      userFormValidationSchema: {
        email: ['required', 'email'],
        password: ['required'],
      },
      errors: {},
      sendingAuth: false,
    };

    this.onBlur = this.onBlur.bind(this);
    this.onChange = this.onChange.bind(this);
    this.tryAuthorizeUser = this.tryAuthorizeUser.bind(this);
    this.doAuthorizeUser = this.doAuthorizeUser.bind(this);
    this.validateField = this.validateField.bind(this);
  }

  componentWillMount() {
    this.redirectToMainIfUserIsAuthorized(this.props.currentUser);
  }

  componentWillReceiveProps(props) {
    this.redirectToMainIfUserIsAuthorized(props.currentUser);
  }


  // TODO: DRY change, blur and submit handlers
  onBlur(event) {
    const field = event.target.name;
    const value = event.target.value;
    const error = this.validateField(field, value);

    return this.setState({
      blurredFields: Object.assign({}, this.state.blurredFields, { [field]: true }),
      errors: Object.assign({}, this.state.errors, error),
    });
  }

  onChange(event) {
    const field = event.target.name;
    const dirtyUser = this.state.formDataUser;
    const value = event.target.value;
    dirtyUser[field] = value;

    let error = {};
    if (this.state.blurredFields[field] || this.state.triedToSubmitForm) {
      error = this.validateField(field, value);
    }

    return this.setState({
      formDataUser: dirtyUser,
      errors: Object.assign({}, this.state.errors, error),
    });
  }

  validateField(field, value) {
    return Validation.generateError(field, value, this.state.userFormValidationSchema);
  }

  tryAuthorizeUser(event) {
    event.preventDefault();
    this.setState({ triedToSubmitForm: true });

    this.validateForm()
      .then((errors) => {
        this.setState({ errors });
        this.doAuthorizeUser();
      })
      .catch((errors) => {
        this.setState({ errors });
      });
  }

  validateForm() {
    const user = this.state.formDataUser;
    const rules = this.state.userFormValidationSchema;
    return new Promise((resolve, reject) => {
      if (Validation.hasErrors(user, rules)) {
        reject(Validation.generateErrors(user, rules));
      } else {
        resolve({});
      }
    });
  }

  doAuthorizeUser() {
    this.setState({ sendingAuth: true });
    this.props.actions.authorizeUser(this.state.formDataUser);
  }

  redirectToMainIfUserIsAuthorized(user) {
    if (user.authorized === true) {
      this.doRedirect(paths.root);
    } else {
      this.setState({ sendingAuth: false });
    }
  }

  doRedirect(path) {
    this.context.router.push(path);
  }

  render() {
    return (
      <LoginPage
        user={this.state.formDataUser}
        onChange={this.onChange}
        onBlur={this.onBlur}
        onSubmit={this.tryAuthorizeUser}
        errors={this.state.errors}
        sendingAuth={this.state.sendingAuth}
      />
    );
  }
}

ManageLoginPage.propTypes = {
  actions: PropTypes.shape({
    authorizeUser: PropTypes.func,
  }).isRequired,
  currentUser: PropTypes.shape({
    id: PropTypes.number,
    authorized: PropTypes.bool.isRequired,
    nickname: PropTypes.string,
    email: PropTypes.string,
    gender: PropTypes.string,
    super: PropTypes.bool,
    permissions: PropTypes.arrayOf(React.PropTypes.oneOfType([
      PropTypes.string,
      // eslint-disable-next-line
      PropTypes.object,
    ])),
    activity: PropTypes.arrayOf(React.PropTypes.shape({
      type: PropTypes.string,
      // eslint-disable-next-line
      object: PropTypes.object
    })),
  }),
};

ManageLoginPage.contextTypes = {
  router: PropTypes.object,
};


function mapStateToProps(state) {
  return {
    currentUser: state.user,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(userActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(ManageLoginPage);



