import React, { PropTypes } from 'react';
import { Link } from 'react-router';
import { ListGroupItem } from 'react-bootstrap';
import capitalize from 'lodash/capitalize';
import adaptGender from '../../utils/adaptGender';

const ActivityRow = (props) => {
  function matchActionType(actionType, gender) {
    const actionTypes = { add: '', edit: '', delete: '' };
    let output = actionTypes[actionType];
    if (output) {
      output = adaptGender(output, gender);
    } else {
      output = capitalize(actionType);
    }
    return output;
  }

  function actionTypeClass(actionType) {
    const actionTypes = { add: 'success', edit: 'warning', delete: 'danger' };
    return actionTypes[actionType] ? actionTypes[actionType] : 'info';
  }

  function matchObjectType(objectType) {
    const objectTypes = { user: '', account: '', transaction: '', token: '' };
    return objectTypes[objectType] ? objectTypes[objectType] : objectType;
  }

  return (
    <ListGroupItem bsStyle={actionTypeClass(props.action.type)}>
      {matchActionType(props.action.type,
          props.gender)} {matchObjectType(props.action.object.type)}&nbsp;
      {/* TODO: will we get link?*/}
      {props.action.object.link ?
        <Link to={props.action.object.link}>
          {props.action.object.title} <small>#{props.action.object.id}</small>
        </Link>
        :
        <span>
          {props.action.object.title} <small>#{props.action.object.id}</small>
        </span>
      }
    </ListGroupItem>
  );
};

ActivityRow.propTypes = {
  action: React.PropTypes.shape({
    type: PropTypes.string,
    // eslint-disable-next-line
    object: PropTypes.object
  }),
  gender: PropTypes.string,
};

export default ActivityRow;



