import React, { PropTypes } from 'react';
import { Button } from 'react-bootstrap';
import Field from '../common/forms/Field';

const LoginForm = ({ onChange, onBlur, onSubmit, errors, sendingAuth }) => (
  <form>
    <Field
      id="loginEmail"
      name="email"
      label=""
      size="large"
      type="email"
      placeholder="raliev@hiconversion.ru"
      onChange={onChange}
      onBlur={onBlur}
      error={errors.email}
    />
    <Field
      id="loginPassword"
      name="password"
      label=""
      size="large"
      type="password"
      placeholder=""
      onChange={onChange}
      onBlur={onBlur}
      error={errors.password}
    />

    <Button
      bsStyle="primary"
      bsSize="large"
      type="submit"
      disabled={sendingAuth}
      onClick={onSubmit}
    >
      {sendingAuth ? '...' : ''}
    </Button>
  </form>
  );

LoginForm.propTypes = {
  onBlur: PropTypes.func,
  onChange: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  errors: PropTypes.shape({
    user: PropTypes.shape({
      type: PropTypes.string,
      hint: PropTypes.string,
    }),
    password: PropTypes.shape({
      type: PropTypes.string,
      hint: PropTypes.string,
    }),
  }),
  sendingAuth: PropTypes.bool,
};

export default LoginForm;



