import React, { PropTypes } from 'react';
import { Navbar, Col } from 'react-bootstrap';
import { Link } from 'react-router';
import SearchForm from './SearchForm';
import UserBlock from './UserBlock';
import style from './Header.scss';
import logo from '../../../assets/images/logo.png';

const Header = props => (
  <header className={style.hicHeader}>
    <Navbar fluid>
      <Col lg={3} md={3} sm={3} xs={12}>
        <Navbar.Header>
          <Navbar.Brand>
            <Link className={style.hicLogoLink} to="/">
              <img className={style.hicLogoPic} src={logo} alt="Humpty" />
                Humpty
            </Link>
          </Navbar.Brand>
        </Navbar.Header>
      </Col>

      {props.user && props.user.authorized &&
        <Col lg={9} md={9} sm={9} xs={12}>
          <Navbar.Form className={style.hicNavbarSearch} pullLeft>
            <SearchForm />
          </Navbar.Form>

          <Navbar.Form pullRight>
            <UserBlock user={props.user} />
          </Navbar.Form>
        </Col>
      }
    </Navbar>
  </header>
  );

Header.propTypes = {
  user: PropTypes.shape({
    loading: PropTypes.bool,
    id: PropTypes.number,
    authorized: PropTypes.bool,
    nickname: PropTypes.string,
    email: PropTypes.string,
    gender: PropTypes.string,
    super: PropTypes.bool,
    permissions: PropTypes.arrayOf(React.PropTypes.oneOfType([
      PropTypes.string,
      // eslint-disable-next-line
      PropTypes.object,
    ])),
  }),
};

export default Header;



