import bsStyles from '../bsStyles';

export default {
  required: {
    rule: value => typeof (value) !== 'undefined' && value !== '',
    hint: () => '  ',
    type: bsStyles.error,
  },
  email: {
    rule: (value) => {
      const emailRegexp = new RegExp(["[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@",
        '(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?'].join(''));
      return emailRegexp.test(value);
    },
    hint: () => '  ',
    type: bsStyles.error,
  },
};



