import UserApi from '../api/UserApi';
import * as types from './actionTypes';

export function loadCurrentUserSuccess(user) {
  return {
    type: types.LOAD_CURRENT_USER_SUCCESS,
    user,
  };
}

export function loadCurrentUser() {
  return dispatch => UserApi.getCurrentUser()
      .then(resp => dispatch(loadCurrentUserSuccess(resp.data)))
      .catch((error) => {
        alert('    :('); // eslint-disable-line
        throw (error);
      });
}

export function authorizeUserSuccess(user) {
  return {
    type: types.AUTHORIZE_USER_SUCCESS,
    user,
  };
}

// noinspection JSUnusedGlobalSymbols
export function authorizeUser(user) {
  return dispatch => UserApi.authorizeUser(user)
      .then((resp) => {
        dispatch(authorizeUserSuccess(resp.data));
        if (!resp.data.authorized) {
          alert('   :('); // eslint-disable-line
        }
      })
      .catch((error) => {
        alert('   :('); // eslint-disable-line
        throw (error);
      });
}



