import React, { PropTypes } from 'react';
import { Jumbotron } from 'react-bootstrap';
import Activity from './Activity';

const HomePage = (props) => {
  if (!props.currentUser.authorized) {
    return <div />;
  }
  return (
    <Jumbotron>
      <div>
        <h1>, {props.currentUser.nickname} :-)</h1>
        <Activity
          actions={props.currentUser.activity}
          gender={props.currentUser.gender}
        />
      </div>
    </Jumbotron>
  );
};

HomePage.propTypes = {
  currentUser: PropTypes.shape({
    id: PropTypes.number,
    authorized: PropTypes.bool.isRequired,
    nickname: PropTypes.string,
    email: PropTypes.string,
    gender: PropTypes.string,
    super: PropTypes.bool,
    permissions: PropTypes.arrayOf(React.PropTypes.oneOfType([
      PropTypes.string,
      // eslint-disable-next-line
      PropTypes.object,
    ])),
    activity: PropTypes.arrayOf(React.PropTypes.shape({
      type: PropTypes.string,
      // eslint-disable-next-line
      object: PropTypes.object
    })).isRequired,
  }),
};

export default HomePage;



