function femalizeVerb(verb) {
  const femaleSuffix = '';
  return (verb + femaleSuffix);
}

function adaptGender(maleVerb, gender) {
  if (gender === 'female') {
    return femalizeVerb(maleVerb);
  }
  return maleVerb;
}

export default adaptGender;



