import React from 'react';
import { Form, FormGroup, FormControl, Button, Glyphicon, InputGroup } from 'react-bootstrap';

const SearchForm = () => (
  <Form>
    <FormGroup>
      <InputGroup>
        <FormControl type="text" />
        <InputGroup.Button>
          <Button>
            <Glyphicon glyph="search" />
          </Button>
        </InputGroup.Button>
      </InputGroup>
    </FormGroup>
  </Form>
    );

export default SearchForm;



