import React, { PropTypes } from 'react';
import { Link } from 'react-router';
import BalanceRow from '../../common/BalanceRow';
import Manager from '../Manager';
import LoginUnder from '../LoginUnder';
import * as helpers from '../../../utils/permissionsHelpers';

const ClientRow = ({ client, permissions }) => (
  <tr>
    {helpers.canRead('id', permissions) &&
      <td>
        <Link to={`/client/${client.id}`}>{client.id}</Link>
      </td>
    }

    {helpers.canRead('username', permissions) &&
      <td>{client.username}</td>
    }

    {helpers.canRead('realname', permissions) &&
      <td>{client.realname}</td>
    }

    {helpers.canRead('email', permissions) &&
      <td>{client.email}</td>
    }

    {helpers.canRead('phone', permissions) &&
      <td><nobr>{client.phone}</nobr></td>
    }

    {helpers.canRead('balance', permissions) &&
      <td>
        <BalanceRow {...client.balance} />
      </td>
    }

    {helpers.canRead('manager', permissions) &&
      <td>
        <Manager {...client.manager} />
      </td>
    }

    {helpers.canRead('loginunder', permissions) &&
      <td>
        <LoginUnder id={client.id} title={client.username} />
      </td>
    }
  </tr>
);

ClientRow.propTypes = {
  client: PropTypes.shape({
    id: PropTypes.number.isRequired,
    username: PropTypes.string.isRequired,
    realname: PropTypes.string,
    email: PropTypes.string,
    phone: PropTypes.string,
    balance: PropTypes.shape({
      RUB: PropTypes.number,
      USD: PropTypes.number,
      EUR: PropTypes.number,
    }),
    manager: PropTypes.shape({
      id: PropTypes.number.isRequired,
      realname: PropTypes.string.isRequired,
    }),
  }),
  permissions: PropTypes.shape({}),
};

export default ClientRow;



