import React, { PropTypes } from 'react';

const LoginUnder = ({ id, title }) => {
  const loginUnderHref = `http://hiconversion.ru/api/account/current/?__login_under=${id}`;
  return (
    <a
      href={loginUnderHref}
      target="_blank"
      rel="noopener noreferrer"
    >
        {title || '...'}
    </a>
  );
};

LoginUnder.propTypes = {
  id: PropTypes.number.isRequired,
  title: PropTypes.string,
};

export default LoginUnder;



