import React, { PropTypes } from 'react';
import { DropdownButton, MenuItem } from 'react-bootstrap';
import Gravatar from 'react-gravatar';

const UserBlock = props => (
  <span>
    <Gravatar email={props.user.email} size={25} />
    <DropdownButton bsStyle="link" title={props.user.nickname} id="user-dropdown">
      <MenuItem eventKey="1"></MenuItem>
      <MenuItem eventKey="2"></MenuItem>
      <MenuItem divider />
      <MenuItem eventKey="3"></MenuItem>
    </DropdownButton>
  </span>
    );

UserBlock.propTypes = {
  user: PropTypes.shape({
    id: PropTypes.number,
    authorized: PropTypes.bool.isRequired,
    nickname: PropTypes.string.isRequired,
    email: PropTypes.string.isRequired,
    gender: PropTypes.string,
    super: PropTypes.bool,
    permissions: PropTypes.arrayOf(React.PropTypes.oneOfType([
      PropTypes.string,
      // eslint-disable-next-line
      PropTypes.object,
    ])),
    activity: PropTypes.arrayOf(React.PropTypes.shape({
      type: PropTypes.string,
      // eslint-disable-next-line
      object: PropTypes.object
    })),
  }),
};

export default UserBlock;



