import * as config from './apiConfig';
import UserApiLocal from './UserApi.local';
import UserApiRemote from './UserApi.remote';

let UserApi;
if (config.ENVIRONMENT === config.ENVIRONMENT_TYPES.test) {
  UserApi = UserApiLocal;
} else {
  UserApi = UserApiRemote;
}

const UserApiImmutable = UserApi;
export default UserApiImmutable;



