import React, { PropTypes } from 'react';
import Spinner from 'react-spinjs';
import assign from 'lodash/assign';

const Preloader = (props) => {
  const defaultOptions = {
    lines: 13, // The number of lines to draw
    length: 0, // The length of each line
    width: 2, // The line thickness
    radius: 35, // The radius of the inner circle
    scale: 1, // Scales overall size of the spinner
    corners: 1, // Corner roundness (0..1)
    color: '#000', // #rgb or #rrggbb or array of colors
    opacity: 0.25, // Opacity of the lines
    rotate: 0, // The rotation offsetirection: 1, // 1: clockwise, -1: counterclockwise
    speed: 1, // Rounds per second
    trail: 60, // Afterglow percentage
    fps: 20, // Frames per second when using setTimeout() as a fallback for CSS
    zIndex: 2e9, // The z-index (defaults to 2000000000)
    className: 'spinner', // The CSS class to assign to the spinner
    top: '50%', // Top position relative to parent
    left: '50%', // Left position relative to parent
    shadow: false, // Whether to render a shadow
    hwaccel: false, // Whether to use hardware acceleration
    position: 'absolute', // Element positioning
  };

  const options = assign({}, defaultOptions, props);

  return <Spinner config={options} />;
};

Preloader.propTypes = {
  lines: PropTypes.number,
  length: PropTypes.number,
  width: PropTypes.number,
  radius: PropTypes.number,
  scale: PropTypes.number,
  corners: PropTypes.number,
  color: PropTypes.string,
  opacity: PropTypes.number,
  rotate: PropTypes.number,
  speed: PropTypes.number,
  trail: PropTypes.number,
  fps: PropTypes.number,
  zIndex: PropTypes.number,
  className: PropTypes.string,
  top: PropTypes.string,
  left: PropTypes.string,
  shadow: PropTypes.bool,
  hwaccel: PropTypes.bool,
  position: PropTypes.string,
};

export default Preloader;



