import React, { PropTypes } from 'react';
import { Link } from 'react-router';

const Manager = ({ id, realname }) => {
  if (id && realname) {
    return <Link to={`/client/${id}`}>{realname}</Link>;
  }
  return <span></span>;
    //  TODO: ?
};

Manager.propTypes = {
  id: PropTypes.number,
  realname: PropTypes.string,
};

export default Manager;



