import React, { PropTypes } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as userActions from '../actions/userActions';
import Layout from './common/layout/Layout';
import * as paths from '../routing/paths';
import './App.scss';

// Export class unwrapped by connect for tests
export class App extends React.Component {
  componentWillMount() {
    this.tryLoginRedirect(this.props.currentUser, this.props.location);
  }

  componentWillReceiveProps(props) {
    this.tryLoginRedirect(props.currentUser, props.location);
  }

  tryLoginRedirect(user, location) {
    const shouldRedirect = user.authorized === false &&
        !user.loading &&
        location.pathname !== paths.login;

    if (shouldRedirect) {
      this.doRedirect(paths.login);
    }
  }

  doRedirect(path) {
    this.context.router.push(path);
  }

  render() {
    return (
      <Layout user={this.props.currentUser}>
        {this.props.children}
      </Layout>
    );
  }
}

App.propTypes = {
  children: PropTypes.element.isRequired,
  currentUser: PropTypes.shape({
    id: PropTypes.number,
    authorized: PropTypes.bool.isRequired,
    nickname: PropTypes.string,
    email: PropTypes.string,
    gender: PropTypes.string,
    super: PropTypes.bool,
    permissions: PropTypes.arrayOf(React.PropTypes.oneOfType([
      PropTypes.string,
      // eslint-disable-next-line
      PropTypes.object,
    ])),
    activity: PropTypes.arrayOf(React.PropTypes.shape({
      type: PropTypes.string,
      // eslint-disable-next-line
      object: PropTypes.object
    })),
  }),
  location: PropTypes.shape({
    pathName: PropTypes.string,
  }),
};

App.contextTypes = {
  router: PropTypes.object,
};

function mapStateToProps(state) {
  return {
    currentUser: state.user,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(userActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(App);



