import React from 'react';
import { Route, IndexRoute } from 'react-router';
import App from '../components/App'; // eslint-disable-line import/no-named-as-default
import * as paths from './paths';
import ManageHomePage from '../components/home/ManageHomePage';
import ManageLoginPage from '../components/login/ManageLoginPage'; // eslint-disable-line import/no-named-as-default
import ManageClientsListPage from '../components/clients/list/ManageClientsListPage';

export default (
  <Route path={paths.root} component={App}>
    <IndexRoute component={ManageHomePage} />
    <Route path={paths.login} component={ManageLoginPage} />
    <Route path="clients" component={ManageClientsListPage} />
  </Route>
);



