import React, { PropTypes } from 'react';

function spacify(num) {
  return num ? num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ' ') : '';
}

function roundCurrency(value, currency) {
  const currencyMultiplier = {
    USD: 0.01,
    RUB: 0.01,
    EUR: 0.01,
  };

  return value * currencyMultiplier[currency];
}

function parseMoney(value, currency) {
  return spacify(roundCurrency(value, currency));
}

const BalanceRow = ({ USD, EUR, RUB }) => (
  <nobr>
    {RUB ? `${parseMoney(RUB, 'RUB')} ` : ''}

    {USD && RUB ? <br /> : ''}
    {USD ? `${parseMoney(USD, 'USD')} $` : ''}

    {EUR && (USD || RUB) ? <br /> : ''}
    {EUR ? `${parseMoney(EUR, 'EUR')} ` : ''}
  </nobr>
);

BalanceRow.propTypes = {
  USD: PropTypes.number,
  RUB: PropTypes.number,
  EUR: PropTypes.number,
};

export default BalanceRow;



